<template>
    <div class="page-container">
        <div class="page-header">
            <div class="header-content">
                <h1>ÊØèÊó•ËÆ∞ÂΩï</h1>
                <p>ËÆ∞ÂΩïÂñµÊòü‰∫∫ÁöÑÊØè‰∏ÄÂ§©</p>
            </div>
            <div class="add-buttons">
                <button class="add-btn weight" @click="showAddDialog('weight')">
                    <i class="el-icon-plus"></i>
                    ËÆ∞ÂΩï‰ΩìÈáç
                </button>
                <button class="add-btn diet" @click="showAddDialog('diet')">
                    <i class="el-icon-plus"></i>
                    ËÆ∞ÂΩïÈ•ÆÈ£ü
                </button>
                <button class="add-btn excretion" @click="showAddDialog('excretion')">
                    <i class="el-icon-plus"></i>
                    ËÆ∞ÂΩïÊéíÊ≥Ñ
                </button>
            </div>
        </div>

        <div class="pets-filter">
            <div class="pets-list">
                <div v-for="pet in pets" :key="pet.id" class="pet-item" :class="{ active: selectedPetId === pet.id }"
                    @click="selectPet(pet.id)">
                    <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
                    <span class="pet-name">{{ pet.name }}</span>
                </div>
            </div>
        </div>

        <div class="type-filter">
            <div v-for="type in recordTypes" :key="type.value" class="type-tag"
                :class="{ active: selectedTypes.includes(type.value) }" @click="toggleType(type.value)">
                <span class="type-icon">{{ type.icon }}</span>
                <span class="type-label">{{ type.label }}</span>
            </div>
        </div>

        <div class="record-list">
            <div v-for="record in filteredRecords" :key="record.id" class="record-card">
                <div class="record-date">{{ formatDate(record.date) }}</div>
                <div class="record-content">
                    <div class="record-header">
                        <div class="pet-info">
                            <img :src="getPetAvatar(record.petId)" :alt="getPetName(record.petId)" class="pet-avatar">
                            <span class="pet-name">{{ getPetName(record.petId) }}</span>
                        </div>
                        <div class="record-type" :class="record.type">
                            <span class="type-icon">{{ getTypeIcon(record.type) }}</span>
                            <span class="type-label">{{ getTypeLabel(record.type) }}</span>
                        </div>
                    </div>
                    <div class="record-body">
                        <template v-if="record.type === 'weight'">
                            <div class="weight-info">
                                <span class="weight-value">{{ record.weight }}kg</span>
                                <span class="weight-change" :class="getWeightChangeClass(record)">
                                    {{ getWeightChange(record) }}
                                </span>
                            </div>
                        </template>
                        <template v-else-if="record.type === 'diet'">
                            <div class="diet-info">
                                <div class="diet-type">{{ record.dietType }}</div>
                                <div class="diet-amount">{{ record.amount }}</div>
                                <div class="diet-time">{{ record.time }}</div>
                            </div>
                        </template>
                        <template v-else-if="record.type === 'excretion'">
                            <div class="excretion-info">
                                <div class="excretion-type">{{ record.excretionType }}</div>
                                <div class="excretion-status">{{ record.status }}</div>
                                <div class="excretion-time">{{ record.time }}</div>
                            </div>
                        </template>
                        <div v-if="record.notes" class="record-notes">
                            <i class="el-icon-notebook-2"></i>
                            <span>{{ record.notes }}</span>
                        </div>
                    </div>
                    <div class="record-actions">
                        <button class="action-btn edit" @click="editRecord(record)">
                            <span class="icon">‚úèÔ∏è</span>
                            <span>ÁºñËæë</span>
                        </button>
                        <button class="action-btn delete" @click="deleteRecord(record)">
                            <span class="icon">üóëÔ∏è</span>
                            <span>Âà†Èô§</span>
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Ê∑ªÂä†/ÁºñËæëËÆ∞ÂΩïÂØπËØùÊ°Ü -->
        <div v-if="showDialog" class="dialog-overlay" @click.self="closeDialog">
            <div class="dialog-content">
                <div class="dialog-header">
                    <h2>{{ dialogTitle }}</h2>
                    <button class="close-btn" @click="closeDialog">&times;</button>
                </div>
                <div class="dialog-body">
                    <div class="form-group">
                        <label>ÈÄâÊã©ÂÆ†Áâ©</label>
                        <div class="pets-select">
                            <div v-for="pet in pets" :key="pet.id" class="pet-option"
                                :class="{ active: form.petId === pet.id }" @click="form.petId = pet.id">
                                <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
                                <span class="pet-name">{{ pet.name }}</span>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label>Êó•Êúü</label>
                        <input type="date" class="form-input" v-model="form.date"
                            :max="new Date().toISOString().split('T')[0]">
                    </div>
                    <div class="form-group">
                        <label>ËÆ∞ÂΩïÁ±ªÂûã</label>
                        <div class="type-select">
                            <div v-for="type in recordTypes" :key="type.value" class="type-option"
                                :class="{ active: form.type === type.value }" @click="form.type = type.value">
                                <span class="type-icon">{{ type.icon }}</span>
                                <span class="type-label">{{ type.label }}</span>
                            </div>
                        </div>
                    </div>
                    <template v-if="form.type === 'weight'">
                        <div class="form-group">
                            <label>‰ΩìÈáç (kg)</label>
                            <input type="number" class="form-input" v-model="form.weight" step="0.1" min="0">
                        </div>
                    </template>
                    <template v-else-if="form.type === 'diet'">
                        <div class="form-group">
                            <label>È•ÆÈ£üÁ±ªÂûã</label>
                            <input type="text" class="form-input" v-model="form.dietType" placeholder="‰æãÂ¶ÇÔºöÁå´Á≤Æ„ÄÅÁΩêÂ§¥„ÄÅÈõ∂È£üÁ≠â">
                        </div>
                        <div class="form-group">
                            <label>È£üÁî®Èáè</label>
                            <input type="text" class="form-input" v-model="form.amount" placeholder="‰æãÂ¶ÇÔºö50g„ÄÅ‰∏Ä‰∏™ÁΩêÂ§¥Á≠â">
                        </div>
                        <div class="form-group">
                            <label>Êó∂Èó¥</label>
                            <input type="time" class="form-input" v-model="form.time">
                        </div>
                    </template>
                    <template v-else-if="form.type === 'excretion'">
                        <div class="form-group">
                            <label>ÊéíÊ≥ÑÁ±ªÂûã</label>
                            <input type="text" class="form-input" v-model="form.excretionType" placeholder="‰æãÂ¶ÇÔºöÂ∞è‰æø„ÄÅÂ§ß‰æøÁ≠â">
                        </div>
                        <div class="form-group">
                            <label>Áä∂ÊÄÅ</label>
                            <input type="text" class="form-input" v-model="form.status" placeholder="‰æãÂ¶ÇÔºöÊ≠£Â∏∏„ÄÅËΩØ‰æø„ÄÅ‰æøÁßòÁ≠â">
                        </div>
                        <div class="form-group">
                            <label>Êó∂Èó¥</label>
                            <input type="time" class="form-input" v-model="form.time">
                        </div>
                    </template>
                    <div class="form-group">
                        <label>Â§áÊ≥®</label>
                        <textarea class="form-textarea" v-model="form.notes" placeholder="Ê∑ªÂä†‰∏Ä‰∫õÂ§áÊ≥®‰ø°ÊÅØ..."></textarea>
                    </div>
                </div>
                <div class="dialog-footer">
                    <button class="cancel-btn" @click="closeDialog">ÂèñÊ∂à</button>
                    <button class="confirm-btn" @click="saveRecord">‰øùÂ≠ò</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Daily',
    data() {
        return {
            pets: [
                {
                    id: 1,
                    name: 'Â∞èÁÅ∞ÁÅ∞',
                    avatar: require('@/assets/normal_cat.png')
                },
                {
                    id: 2,
                    name: 'Ëä±Ëä±',
                    avatar: require('@/assets/cute_cat.png')
                }
            ],
            recordTypes: [
                { value: 'weight', label: '‰ΩìÈáçËÆ∞ÂΩï', icon: '‚öñÔ∏è' },
                { value: 'diet', label: 'È•ÆÈ£üËÆ∞ÂΩï', icon: 'üçΩÔ∏è' },
                { value: 'excretion', label: 'ÊéíÊ≥ÑËÆ∞ÂΩï', icon: 'üöΩ' }
            ],
            dietTypes: [
                { value: 'catFood', label: 'Áå´Á≤Æ', icon: 'ü•´' },
                { value: 'wetFood', label: 'ÊπøÁ≤Æ', icon: 'üçñ' },
                { value: 'treats', label: 'Èõ∂È£ü', icon: 'üç™' },
                { value: 'water', label: 'È•ÆÊ∞¥', icon: 'üíß' }
            ],
            excretionTypes: [
                { value: 'urine', label: 'Â∞è‰æø', icon: 'üíß' },
                { value: 'feces', label: 'Â§ß‰æø', icon: 'üí©' }
            ],
            excretionStatus: [
                { value: 'normal', label: 'Ê≠£Â∏∏', icon: '‚úÖ' },
                { value: 'soft', label: 'ËΩØ‰æø', icon: '‚ö†Ô∏è' },
                { value: 'diarrhea', label: 'ËÖπÊ≥ª', icon: '‚ùå' }
            ],
            selectedPetId: null,
            selectedTypes: [],
            dailyRecords: [
                {
                    id: 1,
                    date: '2024-03-20',
                    petId: 1,
                    petName: 'Â∞èÁÅ∞ÁÅ∞',
                    petAvatar: require('@/assets/normal_cat.png'),
                    type: 'weight',
                    weight: 4.5,
                    change: 0.2,
                    notes: '‰ΩìÈáçÊúâÊâÄÂ¢ûÂä†ÔºåÁªßÁª≠‰øùÊåÅ'
                },
                {
                    id: 2,
                    date: '2024-03-20',
                    petId: 1,
                    petName: 'Â∞èÁÅ∞ÁÅ∞',
                    petAvatar: require('@/assets/normal_cat.png'),
                    type: 'diet',
                    dietType: 'catFood',
                    amount: '100g',
                    time: '08:00',
                    notes: 'Êó©È§êÈ£üÊ¨≤ËâØÂ•Ω'
                }
            ],
            showDialog: false,
            isEditing: false,
            form: {
                petId: null,
                date: '',
                type: '',
                weight: '',
                change: '',
                dietType: '',
                amount: '',
                time: '',
                excretionType: '',
                status: '',
                notes: ''
            }
        }
    },
    computed: {
        dialogTitle() {
            return this.isEditing ? 'ÁºñËæëËÆ∞ÂΩï' : 'Ê∑ªÂä†ËÆ∞ÂΩï'
        },
        filteredRecords() {
            let records = [...this.dailyRecords]

            if (this.selectedPetId) {
                records = records.filter(record => record.petId === this.selectedPetId)
            }

            if (this.selectedTypes.length > 0) {
                records = records.filter(record => this.selectedTypes.includes(record.type))
            }

            return records.sort((a, b) => new Date(b.date) - new Date(a.date))
        }
    },
    methods: {
        selectPet(petId) {
            this.selectedPetId = this.selectedPetId === petId ? null : petId
        },
        toggleType(type) {
            const index = this.selectedTypes.indexOf(type)
            if (index === -1) {
                this.selectedTypes.push(type)
            } else {
                this.selectedTypes.splice(index, 1)
            }
        },
        getPetAvatar(petId) {
            const pet = this.pets.find(p => p.id === petId)
            return pet ? pet.avatar : ''
        },
        getPetName(petId) {
            const pet = this.pets.find(p => p.id === petId)
            return pet ? pet.name : ''
        },
        getTypeIcon(type) {
            const typeInfo = this.recordTypes.find(t => t.value === type)
            return typeInfo ? typeInfo.icon : 'üìù'
        },
        getTypeLabel(type) {
            const typeInfo = this.recordTypes.find(t => t.value === type)
            return typeInfo ? typeInfo.label : 'ÂÖ∂‰ªñ'
        },
        formatDate(date) {
            return new Date(date).toLocaleDateString('zh-CN', {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
                weekday: 'long'
            })
        },
        getWeightChange(record) {
            const prevRecord = this.dailyRecords
                .filter(r => r.petId === record.petId && r.type === 'weight')
                .sort((a, b) => new Date(b.date) - new Date(a.date))[1]

            if (!prevRecord) return 'È¶ñÊ¨°ËÆ∞ÂΩï'

            const change = record.weight - prevRecord.weight
            return `${change > 0 ? '+' : ''}${change.toFixed(1)}kg`
        },
        getWeightChangeClass(record) {
            const change = parseFloat(this.getWeightChange(record))
            if (isNaN(change)) return ''
            return change > 0 ? 'increase' : 'decrease'
        },
        showAddDialog(type) {
            this.isEditing = false
            this.form = {
                petId: null,
                date: new Date().toISOString().split('T')[0],
                type: type,
                weight: '',
                change: '',
                dietType: '',
                amount: '',
                time: '',
                excretionType: '',
                status: '',
                notes: ''
            }
            this.showDialog = true
        },
        editRecord(record) {
            this.isEditing = true
            this.form = { ...record }
            this.showDialog = true
        },
        closeDialog() {
            this.showDialog = false
            this.form = {
                petId: null,
                date: '',
                type: '',
                weight: '',
                change: '',
                dietType: '',
                amount: '',
                time: '',
                excretionType: '',
                status: '',
                notes: ''
            }
        },
        saveRecord() {
            if (this.isEditing) {
                const index = this.dailyRecords.findIndex(r => r.id === this.form.id)
                if (index !== -1) {
                    this.dailyRecords[index] = { ...this.form }
                }
            } else {
                this.dailyRecords.push({
                    ...this.form,
                    id: this.dailyRecords.length + 1
                })
            }
            this.closeDialog()
        },
        deleteRecord(id) {
            if (confirm('Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°ËÆ∞ÂΩïÂêóÔºü')) {
                this.dailyRecords = this.dailyRecords.filter(record => record.id !== id)
            }
        }
    }
}
</script>

<style scoped>

.add-btn.weight {
    background: #4caf50;
}

.add-btn.diet {
    background: #ff9800;
}

.add-btn.excretion {
    background: #2196f3;
}

.add-btn:hover {
    transform: translateY(-2px);
}

.add-btn.weight:hover {
    background: #66bb6a;
}

.add-btn.diet:hover {
    background: #ffa726;
}

.add-btn.excretion:hover {
    background: #42a5f5;
}

@media (max-width: 768px) {
    .add-buttons {
        flex-direction: column;
        width: 100%;
    }

    .add-btn {
        width: 100%;
        justify-content: center;
    }
}
</style>